/* eslint-disable no-trailing-spaces */
import RestofySource from '../data/restofy-source';
import '../component/review-item';

const ReviewInitiator = {
  async init({ form }) {
    await form?.addEventListener('submit', async (event) => {
      event.preventDefault();
      let [id, name, review] = event.target;
      
      id = id.value;
      name = name.value;
      review = review.value;
      if (id === '' && name === '' && review === '') return;

      const { error, customerReviews } = await RestofySource.addReview({
        id,
        name,
        review,
      });
      if (!error) this._renderReviews(customerReviews);
    });
  },

  async _renderReviews(customerReviews) {
    const reviewContainer = document.getElementById('review-container');
    reviewContainer.innerHTML = '';

    customerReviews.map((review) => {
      const reviewItem = document.createElement('review-item');
      reviewItem.review = review;
      return reviewContainer.appendChild(reviewItem);
    });
  },
};

export default ReviewInitiator;
